* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	appearance: none;
}
$black: #000000;
$darkgrey: #303030;
$white: #ffffff;


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
	font-family: 'Icon';
	src: 
		local('fonts/icon'), url('../media/fonts/icon/icon.ttf'),
		local('fonts/icon'), url('../media/fonts/icon/icon.woff2');
	font-display: block;
}
* {
	color: $darkgrey;
}
* {
	font-family: 'Inter', sans-serif;
	font-size: 10;
}

.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'Icon';
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$icons: (
  arrow: 'ea02',
  moon: 'ea03',
  sun: 'ea04',
  reload: 'ea05',
);

@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

@each $name, $code in $icons {
  .icon--#{"" +$name}::before {
    content: unicode($code);
  }
}
.clock {
  border: 10px solid red;
  height: 100vh;
  display: grid;
  grid-template-areas: 
    "quote ."
    "greeting . "
    "time ."
    "location toggle";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr auto auto auto;
  justify-content: start;
  align-items: start;

  &__quote, .quote {
    grid-area: quote;
    
    &__text {
      color: $white;

      &::before { content: "\""; }
      &::after { content: "\""; }
    }

    &__author, .author {
      color: $white;


    }

  }

  &__greeting, .greeting {
    grid-area: greeting;

    &__icon {
      color: $white;
    }

    &__text {
      color: 
      $white;
    }

  }

  &__time, .time {
    grid-area: time;

    &__time {
      color: $white;
    }

    &__timezone {
      color: $white;
    }

  }

  &__location, .location {
    grid-area: location;

    &__city {
      color: $white;
    }

    &__country {
      color: $white;
    }
  }

  &__toggle {
    grid-area: toggle;
  }

}
.page {
	background-image: url('/media/images/background-day.webp');
	width: 100vw;
	min-height: 100vh;
	background-size: cover;
	background-position: center;
}
.panel {
  border: 10px solid blue;

}